import '@/common/iconfont.css';
import Nav from '@/layout/nav.vue'
export default {
	data() {
		return {
			loadMore: {
				iconSize: 24,
				status: 'more',
				showIcon: true,
				iconType: 'snow',
				color: 'red',
				contentText: {
					contentdown: '上拉显示更多',
					contentrefresh: '正在加载...',
					contentnomore: '没有更多数据了'
				}
			},
			bookList: [],
			currentIndex: 0,
			currentIndex1: 0,
			navList: [
				{
					title: '全部',
					url: '全部'
				},
				{
					title: '霸总',
					url: '霸总'
				},
				{
					title: '修真',
					url: '修真'
				},
				{
					title: '恋爱',
					url: '恋爱'
				},
				{
					title: 'VIP',
					url: 'VIP'
				},
				{
					title: '付费',
					url: '付费'
				},
				{
					title: '连载',
					url: '连载'
				},
				{
					title: '完结',
					url: '完结'
				},
				{
					title: '校园',
					url: '校园'
				},
				{
					title: '冒险',
					url: '冒险'
				},
				{
					title: '搞笑',
					url: '搞笑'
				},
			],
			page: 6,
			id: 12,
			swiperheight:500,
		};
	},
	computed: {},
	methods: {
		// isLoading(e) {
		// 	this.isLoad = e.detail.value;
		// },
		getData() {
			uni.request({
				url: 'http://192.168.2.100/showBooklist/' + this.id + '/' + (this.id + this.page),
				success: res => {
					this.bookList = this.bookList.concat(res.data);
					this.id += this.page;
					uni.stopPullDownRefresh();
				}
			});
		},
		setIndex(i){
			this.currentIndex=i
		},
		tabChange(e){
			this.currentIndex = e.detail.current;
		}
	},
	components: {
		'v-nav':Nav
	},
	beforeCreate() {
		uni.request({
			url: 'http://192.168.2.100/showBooklist/0/12', //仅为示例，并非真实接口地址。
			data: {},
			header: {},
			success: res => {
				this.bookList = res.data;
			}
		});
	},
	onPageScroll(e) {
		this.pageScrollTop = Math.floor(e.scrollTop);
	},
	filters: {
		ellipsis(value) {
			if (!value) return '';
			if (value.length > 7) {
				return value.slice(0, 7) + '...';
			}
			return value;
		}
	},
	onReachBottom() {
		this.loadMore.status = 'loading';
	}
};
